.buttons {
    display: flex;
    flex-wrap: wrap;
    size: 20px;
    
}

.buttons__button {
    background: transparent;
    color: teal;
    border: none;
    margin: 0 0 0 20px;
    transition: color 0.3s;
    cursor: pointer;
    
}

@media(max-width:767px) {
    .buttons__button {
        flex-basis: 100%;
        margin: 10px;
    }
}



.buttons__button:hover {
    color: rgb(6, 173, 173);
}
.buttons__button:disabled {
    color: #ccc;
    cursor: not-allowed;
}